import { useState, useEffect } from "react"

function useStats() {
    const [ stats, setStats ] = useState()

    useEffect(() => {

        async function fetchData() {
            console.log("Fetching data")

            let data = await fetch("https://alerta-corona-backend.herokuapp.com/markers",
            { method: 'GET', mode: 'no-cors' }).then(res => res.json());
            
            setStats(data)
        }

        fetchData()
    }, [])

    return stats
}

export default function IndexPage() {
    const stats = useStats()
    console.log(stats)
    return <h1>Great!</h1>
}